import { tt } from "../../fe/BabyMarkdown"
import { minuteMs, secondMs } from "../../fe/Date"
import { defer } from "../../fe/Defer"
import { delay } from "../../fe/Delay"
import { lazy } from "../../fe/Lazy"
import { fmtPct } from "../../fe/Number"
import { until } from "../async/until"
import { Settings } from "../settings/Settings"
import { CpuUsage, isTooBusy } from "../work/CpuUsage"
import { HealthCheck } from "./HealthCheck"

export const systemLoadHealthCheck = defer(() =>
  HealthCheck.for({
    section: "System",
    id: "system-load",
    pendingMsg: "Checking system load",
    settings: ["cpuLoadPercent"],
    rejectIsWarn: true,
    ttlMs: minuteMs,
    later: async () => {
      const busyPct = CpuUsage.instance().busyPct()
      const tooBusy = isTooBusy()
      if (busyPct == null || tooBusy) {
        void refreshLater()
      }
      const level = tooBusy ? "stop-sync" : "ok"
      const msg =
        "CPU utilization is " +
        fmtPct(busyPct) +
        ".\n" +
        (tooBusy
          ? "PhotoStructure sync is paused until CPU utilization drops below"
          : "PhotoStructure sync will pause if CPU utilization exceeds") +
        " " +
        tt(Settings.cpuLoadPercent.key) +
        ": " +
        fmtPct(Settings.cpuLoadPercent.valueOrDefault)
      return { level, msg }
    }
  })
)

const refreshLater = lazy(async () => {
  await delay(10 * secondMs)
  await until(() => CpuUsage.instance().busyPct(), {
    timeoutMs: 10 * secondMs,
    intervalMs: secondMs
  })
  await systemLoadHealthCheck().refresh()
}, minuteMs)
